import CaculPrice from '@/components/caculPrice.vue'
export default {
	components:{
		CaculPrice,
	},
	data() {
		return {
			orderType:1,// 1 预约订单 2 待付款订单
			orderInfo:'',
			erectorInfo:'',
			couPonList:'',
			showCouPon:false,
			checkCoupon:false,
			couponTitle:'点击选择优惠卷',
			couPonId:0
		};
	},
	onLoad(option) {
		if(option.orderId){
			this.orderId = option.orderId
		}
		if(option.orderType){
			this.orderType = option.orderType;
			if(option.orderType == '2'){
				this.queryCoupon();
			}	
		}
		this.getDetail();
		
	},
	methods:{
		getDetail(){
			let _this = this;
			this.$http.request({
				url:this.$api.Query_OrderDetail,
				data:{
					orderId:_this.orderId
				},
				method:'POST'
			}).then( res => {
				let data = res.data;
				this.orderInfo = data.orderInfo;
				this.erectorInfo = data.erectorInfo || '';
			})
		},
		preImage(url){
			uni.previewImage({
				urls:[url],
				success() {
					
				}
			})
		},
		queryCoupon(){
			let _this = this;
			this.$http.request({
				url:this.$api.NeedCoupon,
				data:{
					orderId:_this.orderId
				},
				method:'POST'
			}).then(res => {
				this.couPonList = res.data;
			})
		},
		chooseCoupon(){
			if(!this.couPonList){
				this.$pop.msg('暂无可用优惠卷!!!')
				return;
			}
			this.showCouPon = true;
		},
		chooseAmount(item){
			if(item){
				this.$set(item,"checked",true);
				this.checkCoupon = false;
				this.couPonId = item.id;
				this.couponTitle = item.title;
			}else{
				let _this = this;
				this.checkCoupon = true;
				this.couPonId = 0;
				this.couponTitle = "不使用优惠卷";
				this.couPonList.forEach(function(item){
					_this.$set(item,"checked",false);
				})
				
			}
			
		},
		noUseCoupon(){
			this.checkCoupon = !this.checkCoupon;
			
			if(this.checkCoupon){
				this.couponTitle = "不使用优惠卷";
			}		
		},
		moveHandle(){
			
		},
		hideCoupon(){
			this.showCouPon = false;
		},
		pay(){
			console.log('zhifu')
			let _this = this;

			this.$http.request({
				url:this.$api.WeixinPay,
				data:{
					orderId:_this.orderId,
					couPonId:_this.couPonId
				},
				method:'POST'
			}).then(res => {
				if(!res.errno){
					console.log("res: " + JSON.stringify(res));
					let paymentData = res.data;
					_this.WeixinPay(paymentData);
				}else{
					_this.$pop.msg('获取支付失败,请稍后重试!!')
				}
				
				
			})
















			
			
			
		},

		WeixinPay(pay){
			console.log('开启支付')
			uni.requestPayment({
			    timeStamp: pay.timeStamp,
			    nonceStr: pay.nonceStr,
			    package: pay.packageValue,
			    signType: pay.signType,
			    paySign: pay.paySign,
			    success: (res) => {
					console.log(res)
			        uni.showToast({
			            title: "感谢您的赞助!"
			        })
			    },
			    fail: (res) => {
			        uni.showModal({
			            content: "支付失败,原因为: " + res
			                .errMsg,
			            showCancel: false
			        })
			    },
			    complete: () => {
			        this.loading = false;
			    }
			})
		},


























	},
	computed:{
		
	}
}
